import 'hammerjs';

import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpModule } from '@angular/http';
import { FormsModule }   from '@angular/forms';
import { MdButtonModule, MdCheckboxModule, MdListModule, MdCardModule, MdTableModule, MdInputModule, MdGridListModule, MdSliderModule, MdRadioModule } from '@angular/material';

import { AppComponent } from './app.component';
import { FileInputComponent } from './components/file-input/file-input.component';
import { PredictionComponent } from './components/prediction/prediction.component';
import { PredictionPanelComponent } from './components/prediction-panel/prediction-panel.component';

@NgModule({
  declarations: [
    AppComponent,
    PredictionComponent,
    FileInputComponent,
    PredictionPanelComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpModule,
    FormsModule,
    MdButtonModule,
    MdCheckboxModule,
    MdListModule,
    MdCardModule,
    MdTableModule,
    MdInputModule,
    MdGridListModule,
    MdSliderModule,
    MdRadioModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



